//
// checkbox
//
.control {
  font-family: lato;
  display: block;
  position: relative;
  padding-left: 25px;
  margin-bottom: 5px;
  padding-top: 1px;
  cursor: pointer;

  input {
    position: absolute;
    z-index: -1;
    opacity: 0; } }

.control_indicator {
  position: absolute;
  top: 3px;
  left: 0;
  height: 13px;
  width: 13px;
  background: #ffffff;
  border: 1px solid #ebebeb;
  border-radius: 0px; }

.control {
  &:hover input ~ .control_indicator {
    background: #cccccc; }

  input {
    &:focus ~ .control_indicator {
      background: #cccccc; }

    &:checked ~ .control_indicator {
      background: #f16d7f; } }

  &:hover input:not([disabled]):checked ~ .control_indicator {
    background: #e6647d; }

  input {
    &:checked:focus ~ .control_indicator {
      background: #e6647d; }

    &:disabled ~ .control_indicator {
      background: #e6e6e6;
      opacity: 0.6;
      pointer-events: none; } } }

.control_indicator:after {
  box-sizing: unset;
  content: '';
  position: absolute;
  display: none; }

.control input:checked ~ .control_indicator:after {
  display: block; }

.control-checkbox {
  .control_indicator:after {
    left: 4px;
    top: 1px;
    width: 2px;
    height: 5px;
    border: solid #ffffff;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg); }

  input:disabled ~ .control_indicator:after {
    border-color: #7b7b7b; } }
//
//range
//
//

input[type=range] {
  -webkit-appearance: none;
  margin: 10px 0;
  width: 100%;

  &:focus {
    outline: none; }

  &::-webkit-slider-runnable-track {
    width: 100%;
    height: 6px;
    cursor: pointer;
    animate: 0.2s;
    box-shadow: 0px 0px 0px #000000;
    background: #f0f0f0;
    border-radius: 3px;
    border: 0px solid #000000; }

  &::-webkit-slider-thumb {
