.control {
  font-family: lato;
  display: block;
  position: relative;
  padding-left: 25px;
  margin-bottom: 5px;
  padding-top: 1px;
  cursor: pointer; }
  .control input {
    position: absolute;
    z-index: -1;
    opacity: 0; }

.control_indicator {
  position: absolute;
  top: 3px;
  left: 0;
  height: 13px;
  width: 13px;
  background: #ffffff;
  border: 1px solid #ebebeb;
  border-radius: 0px; }

.control:hover input ~ .control_indicator {
  background: #cccccc; }

.control input:focus ~ .control_indicator {
  background: #cccccc; }

.control input:checked ~ .control_indicator {
  background: #f16d7f; }

.control:hover input:not([disabled]):checked ~ .control_indicator {
  background: #e6647d; }

.control input:checked:focus ~ .control_indicator {
  background: #e6647d; }

.control input:disabled ~ .control_indicator {
  background: #e6e6e6;
  opacity: 0.6;
  pointer-events: none; }

.control_indicator:after {
  box-sizing: unset;
  content: '';
  position: absolute;
  display: none; }

.control input:checked ~ .control_indicator:after {
  display: block; }

.control-checkbox .control_indicator:after {
